<template>
  <v-dialog
     v-model="DialogCreateDevice"
      max-width="650px"
      class=""
      style="magin-top=0px;border-radius: 15px; "
      overlay-opacity="0.8"
  >
    <v-card class align="center" style="border-radius: 15px;">
      <v-card-title
          class="headline font-weight-bold white--text black"
          primary-title
        >
          Create Device
          <v-spacer></v-spacer>
          <v-btn icon class="float-right black" @click="close()">
            <v-icon class="white" style="border-radius: 5px;">mdi-close-thick</v-icon>
          </v-btn>
        </v-card-title>
      <v-card-text>
        <v-container>
          <v-form>
            <v-row class="ml-0" align="center" justify="center">
              <v-col cols="3" align="left">
                <p class="font-weight-bold">Meter id:</p>
              </v-col>
              <v-col cols="7" align="left">
                <v-text-field
                style="border-radius: 10px;"
                  color="red darken-4"
                  label="mm_1910"
                  v-model="meterid"
                  type="text"
                  dense
                  solo
                  filled
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row class="ml-0" align="center" justify="center">
              <v-col cols="3" align="left">
                <p class="font-weight-bold">Meter name:</p>
              </v-col>
              <v-col cols="7" align="left">
                <v-text-field
                style="border-radius: 10px;"
                  color="red darken-4"
                  label="motor"
                  v-model="metername"
                  type="text"
                  dense
                  solo
                  filled
                ></v-text-field>
              </v-col>
            </v-row>
            <v-row class="ml-0" align="center" justify="center">
              <v-col cols="3" align="left">
                <p class="font-weight-bold">Department:</p>
              </v-col>
              <v-col cols="7" align="left">
                <v-text-field
                style="border-radius: 10px;"
                  color="red darken-4"
                  label="R&D"
                  v-model="department"
                  type="text"
                  dense
                  solo
                  filled
                ></v-text-field>
              </v-col>
            </v-row>

            <div class="text-center">
              <v-row>
                <v-col>
                  <v-btn style="border-radius: 10px;" block color="white" @click="Cancel()">Cancel</v-btn>
                </v-col>
                <v-col>
                  <v-btn style="border-radius: 10px;" block color="black" dark @click="validate_before_submit()">Submit</v-btn>
                </v-col>
              </v-row>
            </div>
          </v-form>
        </v-container>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>
import Swal from "sweetalert2";
export default {
  data() {
    return {
      loading: false,
      meterid: undefined,
      metername: undefined,
      department: undefined,
      DialogCreateDevice: false
    };
  },
  created() {
      this.get_data_Device();

  },
  watch: {},
  methods: {
    open() {
      this.DialogCreateDevice = true;
    },
    clear_data_addDevice() {
      this.meterid = undefined;
      this.metername = undefined;
      this.department = undefined;
    },
    Cancel() {
      this.DialogCreateDevice = false;
      this.clear_data_addDevice();
    },
    close() {
      this.DialogCreateDevice = false;
      this.clear_data_addDevice();
    },
    validate_before_submit() {
      if (
        this.meterid === undefined ||
        this.metername === undefined ||
        this.department === undefined
      ) {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "Something went wrong!"
        });
      } else {
        this.Submit();
      }
    },
    Submit() {
       var source = {
         user_account: this.user_login,
         meter_id:this.meterid,
         meter_name:this.metername,
         department: this.department
       } 
      //  console.log(source);
      this.axios({
        method: "POST",
        url: this.$store.state.url_axios + "/api/v1/meters",
        data: source,
        headers: { Authorization: "Bearer " + localStorage.token }
      }).then(response => {
        // console.log(response.data);
        this.resultcheck = response.data;
        this.result = response.data.result;
       
        if (this.resultcheck["message"] === "success") {
          this.$emit('getListdevice')
          Swal.fire({
              icon: 'success',
              title: 'Success',
              text: 'Create device success',
            })
          this.clear_data_addDevice();
          this.DialogCreateDevice = false;

        }
   
        else {
          Swal.fire({
            icon: "error",
            title: "Oops...",
            text: "Something went wrong!",
          });
        }
      }).catch(error => {
        // console.log(error.response);
          if (error.response.data.error_message === "Measurement Not Found") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "ไม่พบข้อมูลกราฟ วัน-เวลาที่ค้นหา !!"
            });
          }
               else   if (error.response.data.error_message === "Meter exist") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "Duplicate meter name Can not be added",
            });
          }
           else {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "กรุณาตรวจสอบการเชื่อมต่อ"
            });
          }
        });
    },
      get_data_Device() {
      let data = JSON.parse(window.localStorage.getItem("user_login"));
      // console.log(data)
      if (data === null) {
        this.user_login = "";
      } else {
        this.user_login = data;
      }
    },
  }
};
</script>
<style >
.color_blue {
  color: #40c4ff;
}
</style>





