                class="v-sheet--offset mx-auto white--text"
                elevation="12"
                max-width="calc(100% - 32px)"
                
              >
                <v-row>
                  <v-col></v-col>
                </v-row>
                <v-row align="center" justify="left">
                  <v-col cols="9">
                    <div
                      class="font-weight-bold truncate"
                      style="font-size: 21px"
                    >
                      {{ items.machineName }}
                    </div>
                  </v-col>

                  <v-col cols="2">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-icon large v-on="on" color="white"
                          >mdi-gauge-low</v-icon
                        >
                      </template>
                      <span>gauge device</span>
                    </v-tooltip>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col></v-col>
                </v-row>
              </v-sheet>
              <v-card-text class="pt-0 left">
                <div
                  class="mb-2 ml-2 font-weight-medium truncate"
                  style="text-align-last: left; font-size: 15px"
                >
                  <span style="font-size: 16px">Department:</span>
                  {{ items.department }}
                </div>

                <div
                  class="mt-3 mb-1 h5 font-weight-medium ml-2 truncate"
                  style="text-align-last: left; font-size: 15px"
                >
                  <span style="font-size: 16px"
                    >MachineID:
                    <input
                      disabled
                      type="text"
                      v-model="items.machineID"
                      id="myInput"
                  /></span>
                  <v-icon
                    small
                    class=""
                    @click="Open_device(items.machineID, 1)"
                    >mdi-content-copy</v-icon
                  >
                </div>
              </v-card-text>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
      <!-- delete -->
      <v-row
        v-if="this.mode == 'delete'"
        style="text-align: -webkit-center"
        class="mt-2"
      >
        <v-col cols="4" sm-4 v-for="items in array_getdata" :key="items">
          <v-hover v-slot:default="{ hover }" open-delay="200">
            <v-card
              class="mt-5 mx-auto"
              style="border-radius: 10px"
              max-width="320"
              @click="Open_delete_Device(items)"
              :elevation="hover ? 16 : 2"
            >
              <v-sheet
                color="black"
                style="border-radius: 10px"
                class="v-sheet--offset mx-auto white--text"
                elevation="12"
                max-width="calc(100% - 32px)"
              >
                <v-row>
                  <v-col></v-col>
                </v-row>
                <v-row align="center" justify="left">
                  <v-col cols="9">
                    <div
                      class="font-weight-bold truncate"
                      style="font-size: 21px"
                    >
                      {{ items.machineName }}
                    </div>
                  </v-col>

                  <v-col cols="2">
                    <v-tooltip bottom>
                      <template v-slot:activator="{ on }">
                        <v-icon large v-on="on" color="white"
                          >mdi-gauge-low</v-icon
                        >
                      </template>
                      <span>gauge device</span>
                    </v-tooltip>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col></v-col>
                </v-row>
              </v-sheet>
              <v-card-text class="pt-0 left">
                <div
                  class="mt-3 mb-1 h5 font-weight-medium ml-2 truncate"
                  style="text-align-last: left; font-size: 15px"
                >
                  <span style="font-size: 16px">Department:</span>
                  {{ items.department }}
                </div>
                <div
                  class="mt-3 mb-1 h5 font-weight-medium ml-2 truncate"
                  style="text-align-last: left; font-size: 15px"
                >
                  <span style="font-size: 16px">MachineID:</span>
                  {{ items.machineID }}
                </div>
              </v-card-text>
              <v-fade-transition class="animate slideInUp">
                <v-overlay
                  v-if="hover"
                  absolute
                  color="grey lighten-1"
