                >
                  <v-icon size="90px" style="margin-top: 20%" color="red"
                    >mdi-delete-circle-outline</v-icon
                  >
                </v-overlay>
              </v-fade-transition>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
      <v-row align="center" justify="center" class="ml-2 mr-5">
        <v-col
          class="mt-0"
          cols="6"
          style="text-align: -webkit-center"
          height="250px"
        ></v-col>
        <!-- <v-col cols="6" sm="6" md="6" height="400px">
          <v-card flat style="background-color: #eceff1">
            <v-card-text></v-card-text>
          </v-card>
        </v-col> -->
      </v-row>
    </v-container>
    <v-speed-dial
      class="mb-12 mr-1"
      v-model="create"
      fab
      fixed
      right
      bottom
      hover
      :transition="transition"
    >
      <template v-slot:activator>
        <v-btn
          v-if="mode === 'create'"
          v-model="create"
          bottom
          large
          color="blue darken-2"
          dark
          fab
        >
          <v-icon v-if="create" large>mdi-close</v-icon>
          <v-icon v-else large>mdi-plus-circle</v-icon>
        </v-btn>

        <v-tooltip v-model="show" left v-if="mode != 'create'">
          <template v-slot:activator="{ on, attrs }">
            <v-btn
              v-bind="attrs"
              v-on="on"
              @click="exit_mode_delete()"
              bottom
              large
              color="black darken-2"
              dark
              fab
            >
              <v-icon>mdi-arrow-right-bold</v-icon>
            </v-btn>
          </template>
          <span>Exit Delete Mode</span>
        </v-tooltip>
      </template>
      <v-btn fab dark color="green" @click="btn_create()">
        <v-icon>mdi-plus</v-icon>
      </v-btn>
      <v-btn fab dark color="red" @click="btn_delete()">
        <v-icon>mdi-delete</v-icon>
      </v-btn>
    </v-speed-dial>
    <DialogCreateDevice
      ref="DialogCreateDevice"
      @getListdevice="API_getdata_List_device"
    ></DialogCreateDevice>
  </div>
