<script>
import DialogCreateDevice from "@/components/DialogCreateDevice.vue";
import Swal from "sweetalert2";
export default {
  name: "DashboardDevice",
  components: {
    DialogCreateDevice,
  },
  data() {
    return {
      counters: 0,
      alert: false,
      Index: "",
      attrs: undefined,
      Name: "",
      fab: false,
      loading: false,
      array_getdata: [],
      mode: "create",
      create: "",
      loadingdelete: false,
      color_delete: "",
      show: false,
      show1: false,
      show2: false,
      transition: "slide-y-reverse-transition",
      array_devicedata: [],
      status: 0,
    };
  },
  created() {
    this.API_getdata_List_device();
    this.$store.commit("data_interval_get_data", this.counters);
  },
  methods: {
    onScroll(e) {
      if (typeof window === "undefined") return;
      const top = window.pageYOffset || e.target.scrollTop || 0;
      this.fab = top > 20;
    },
    toTop() {
      this.$vuetify.goTo(0);
    },
    Open_device(items, status) {
      // console.log(items);
      // console.log(status);
      if (status == "1") {
        var copyText = document.getElementById("myInput");
        console.log(copyText);

        /* Select the text field */
        copyText.select();
        copyText.setSelectionRange(0, 99999); /* For mobile devices */

        /* Copy the text inside the text field */
        navigator.clipboard.writeText(items);

        /* Alert the copied text */
        alert("Copied : " + items);
        // items.select();
        // document.execCommand("copy");
      } else {
        var arrayDevice = this.array_devicedata;
        var Device = {
          Index: arrayDevice.length + 1,
          machineID: items.machineID,
          machineName: items.machineName,
          department: items.department,
        };
        arrayDevice.push(Device);
        window.localStorage.setItem("arrayDevice", JSON.stringify(arrayDevice));
        window.localStorage.setItem(
          "machineID",
          JSON.stringify(items.machineID)
        );
        window.localStorage.setItem(
          "machineName",
          JSON.stringify(items.machineName)
        );

        this.$router.push("/Device");
      }
    },
    Open_delete_Device(items) {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        cancelButtonColor: "#3085d6",
        confirmButtonText: "Yes, delete it!",
      }).then((result) => {
        if (result.value) {
          this.API_Delete_Device(items.machineID);
        }
      });
    },
    API_Delete_Device(machineID) {
      this.axios({
        method: "DELETE",
        url:
          this.$store.state.url_axios + "/api/v1/meters?meter_id=" + machineID,
        headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          this.resultcheck = response.data;
          this.result = response.data.result;

          if (this.resultcheck["message"] === "success") {
            this.$emit("getListdevice");
            Swal.fire({
              icon: "success",
              title: "Success",
              text: "Delete device success",
            });
            this.API_getdata_List_device();
          } else {
            Swal.fire({
              icon: "error",
              title: "error",
              text: "Can not be deleted",
            });
          }
        })
        .catch((error) => {
          if (error.response.data.error_message === "Measurement Not Found") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "Can not be deleted",
            });
          } else if (error.response.data.error_message === "Meter exist") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "Can not be deleted",
            });
          } else {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "Can not be deleted",
            });
          }
        });
    },
    btn_create() {
      this.$refs.DialogCreateDevice.open();
    },
    btn_delete() {
      this.mode = "delete";
      this.snackbar_delete = true;
      this.$message.error("Delete Mode");
    },
    exit_mode_delete() {
      this.mode = "create";
      this.create = "";
    },
    API_getdata_List_device() {
      this.loading = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_axios + "api/v1/meters",
        headers: { Authorization: "Bearer " + localStorage.token },
      })
        .then((response) => {
          this.loading = false;
          var result = response.data;

          if (result.len === 0) {
            this.$swal({ title: "ไม่มีข้อมูล", type: "info" });

            this.array_getdata = result["result"];
            this.loading = false;
          } else {
            this.array_getdata = result["result"];
            this.loading = false;
          }
        })
        .catch((error) => {
          if (error.response.data.error_message === "Measurement Not Found") {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "No data",
            });
          } else if (error.response.statusText === "UNAUTHORIZED") {
            this.$router.push("/login");
            this.$message.warning("Time out !!!");
          } else if (error.response.statusText === "UNPROCESSABLE ENTITY") {
            this.$router.push("/login");
          } else {
            Swal.fire({
              icon: "error",
              title: "Oops...",
              text: "กรุณาตรวจสอบการเชื่อมต่อ",
            });
          }
        });
    },
  },
};
</script>

